import { useEffect } from 'react';
import { debounce } from '@yomua/y-screw';

const DEFAULT_OPTIONS = {
    delay: 0,
    isRefreshItself: false,
    returnEffect: () => { },
};
// 此 hook 的监听器将被防抖
function useWindowEventListener(eventName, listenCallback, effect, options = DEFAULT_OPTIONS) {
    if (!eventName) {
        return;
    }
    const { delay = 0, isRefreshItself = false, returnEffect = () => { }, } = options;
    useEffect(() => {
        const debounceCallback = debounce(listenCallback, delay, {
            isRefreshItself,
        });
        window.addEventListener(eventName, debounceCallback);
        return () => {
            window.removeEventListener(eventName, debounceCallback);
            returnEffect && returnEffect();
        };
    }, effect);
}

export { useWindowEventListener as default };
